a {
  color: var(--link-text-color);
  outline: none;
  text-decoration: none;
}

a:hover {
  color: var(--link-hover-color);
  text-decoration: underline;
}

abbr[title] {
  -webkit-text-decoration-skip-ink: none;
  text-decoration-skip-ink: none;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
}

[type="checkbox"] {
  padding: 0;
}

[type="checkbox"]:not(:checked), [type="checkbox"]:checked {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

[type="checkbox"] + span {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  height: 20rem;
  padding-left: 25rem;
  font-size: 16rem;
  line-height: 20rem;
  display: inline-block;
  position: relative;
}

[type="checkbox"] + span:before, [type="checkbox"] + span:after {
  content: "";
  z-index: 0;
  position: absolute;
  left: 0;
}

[type="checkbox"] + span:after {
  z-index: 0;
  border: 1rem solid var(--primary-text-color);
  background-color: #0000;
  width: 16rem;
  height: 16rem;
  top: 1rem;
  left: 2rem;
}

[type="checkbox"] + span:before {
  z-index: 1;
  border: 2rem solid var(--primary-text-color);
  border-radius: 1rem;
  width: 0;
  height: 0;
  top: 2rem;
  left: 8rem;
  transform: rotateZ(37deg);
}

[type="checkbox"]:not(:checked) + span:before {
  border: 3rem solid #0000;
}

[type="checkbox"]:checked + span:before {
  border-top: 2rem solid #0000;
  border-left: 2rem solid #0000;
  border-right: 2rem solid var(--primary-text-color);
  border-bottom: 2rem solid var(--primary-text-color);
  width: 5rem;
  height: 10rem;
}

:not(pre) code {
  background: var(--pre-bg-color);
  box-sizing: border-box;
  color: var(--code-text-color);
  border-radius: 5px;
  padding: .2em .4em;
  font-size: 80%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-style: inherit;
  margin: 12rem 0;
  font-weight: 500;
}

h1 {
  margin-top: 48rem / 1.125;
  clear: both;
  margin-top: 60rem;
  margin-bottom: 24rem;
  font-size: 48rem;
  font-weight: 600;
  line-height: 1.04167;
}

h2 {
  margin-top: 28rem / 1.125;
  clear: both;
  border-bottom: 4rem solid;
  margin-top: 60rem;
  margin-bottom: 24rem;
  font-size: 28rem;
  line-height: 1.5;
}

h3 {
  margin-top: 24rem / 1.125;
  margin-top: 40rem;
  margin-bottom: 16rem;
  font-size: 24rem;
  line-height: 1.41667;
}

h4 {
  margin-top: 20rem / 1.125;
  margin-top: 24rem;
  font-size: 20rem;
  font-weight: 600;
  line-height: 1.5;
}

h5 {
  margin-top: 18rem / 1.125;
  text-transform: uppercase;
  font-size: 18rem;
  font-weight: 600;
  line-height: 1.33333;
}

h6 {
  margin-top: 16rem / 1.125;
  text-transform: uppercase;
  font-size: 16rem;
  font-weight: 600;
  line-height: 1.375;
}

hr {
  color: var(--separator-color);
  background-color: var(--separator-color);
  border: 0;
  margin: 16rem 0;
}

hr:not([size]) {
