}

img {
  vertical-align: middle;
  max-width: 100%;
}

mark {
  background-color: var(--mark-bg-color);
  padding: 2px;
}

p {
  margin-top: 0;
  margin-bottom: 12rem;
}

pre {
  white-space: pre;
  background: var(--pre-bg-color);
  border-radius: 3px;
  margin: 16rem auto;
  overflow: auto;
}

pre code {
  color: inherit;
  white-space: pre;
  background: none;
  border: none;
  font-family: Source Code Pro;
  font-size: 14rem;
  display: block;
  background: none !important;
  padding: 16rem !important;
}

small {
  font-size: 12rem;
}

strong {
